'use strict';

$(document).ready(function () {
	if ($('#add-event-form').length > 0) {
		// Validation for add-event-form
		moment().format();

		// additional method for future date validation
		jQuery.validator.addMethod('futureDate', function (value, element) {
			var startDate = $('#start_date').val();
			var now = moment();

			if (moment(startDate) <= now) {
				return false;
			} else {
				return true;
			}
		});

		// additional method for date matching validation
		jQuery.validator.addMethod('dateChecker', function (value, element) {

			var startDate = $('#start_date').val();
			var endDate = $('#end_date').val();

			if (moment(startDate) >= moment(endDate)) {
				return false;
			} else {
				return true;
			}
		});

		$.validator.setDefaults({
			errorClass: 'invalid',
			validClass: 'valid',
			errorPlacement: function errorPlacement(error, element) {
				$(element).closest('form').find('input[id=\'' + element.attr('id') + '\']').attr('class', 'validate invalid');
			}
		});

		$('#add-event-form').validate({
			rules: {
				start_date: {
					futureDate: true
				},
				end_date: {
					dateChecker: true
				}
			},
			onfocusout: function onfocusout(element) {
				this.element(element);
			}
		});

		$('#name').on('blur', function () {
			$('#add-event-form').validate().element(this);
		});
		$('#event-type').on('blur', function () {
			$('#add-event-form').validate().element(this);
		});
		$('#host').on('blur', function () {
			$('#add-event-form').validate().element(this);
		});
		$('#start_date').on('blur', function () {
			$('#add-event-form').validate().element(this);
		});
		$('#end_date').on('blur', function () {
			$('#add-event-form').validate().element(this);
		});
		$('#guest-list').on('blur', function () {
			$('#add-event-form').validate().element(this);
		});
		$('#location').on('blur', function () {
			$('#add-event-form').validate().element(this);
		});
	}
});
'use strict';

$(document).ready(function () {
	$.getJSON('../data/events.json', function (events) {
		$.each(events, function (i, event) {
			var eventPanel = '<div class="col s12 m4">' + '<div class="card-panel">' + '<h4 class="card-title name">' + event.name + '</h4>' + '<br>' + '<p class="type">Type:' + event.type + '</p>' + '<p class="host">Host:' + event.host + '</p>' + '<p class="start-date">Start: ' + event.start_date + '</p>' + '<p class="end-date">End: ' + event.end_date + '</p>' + '<p class="guests">Guests:</p>' + '<ul class="guest-list" id=' + event.id + '>' + '</ul>' + '<p class="location">Location: ' + event.location + '</p>' + '<p class="message">Message: ' + event.message + '</p>' + '</div>' + '</div>';

			$('#events').append(eventPanel);

			var listItemID = '#' + event.id;

			$.each(event.guest_list, function (i, guest) {
				var guestListItem = '<li class="guest">' + guest + '</li>';
				$(listItemID).append(guestListItem);
			});
		});
	});
});
